import React from "react";
import { Card, Col, Container, Nav, Navbar, Row } from "react-bootstrap";
import myimage from "./images/suhas.jpg";
import "./Home.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub, faLinkedin } from "@fortawesome/free-brands-svg-icons";
import { faEnvelope } from "@fortawesome/free-solid-svg-icons";
import Publications from "./Publications";
import Works from "./Works";

const Home = () => {
  return (
    <div>
      <div>
        <Navbar bg="dark" variant="dark">
          <Container>
            <Navbar.Brand href="#home">Suhas Bhairav</Navbar.Brand>
          </Container>
        </Navbar>
      </div>
      <div className="home__content">
        <div className="container">
          <div className="row">
            <div className="col-md-6">
              <Card className="home__card shadow-lg p-3 mb-5 bg-body rounded">
                <div className="row">
                  <div className="col-md-6">
                    <Card className="profile__image__content">
                      <Card.Img
                        variant="top"
                        className="profile__image rounded-circle"
                        src={myimage}
                      />
                    </Card>
                  </div>
                  <div className="col-md-6 d-flex justify-content-around">
                    <Card>
                      <Card.Body>
                        <Card.Title>
                          <h2>
                            <b>Suhas Bhairav</b>
                          </h2>
                        </Card.Title>
                        <div className="profile__links">
                          <a
                            className="m-2 anchor-style"
                            href="https://www.github.com/suhasbhairav"
                            target="_blank"
                            rel="noreferrer"
                          >
                            <FontAwesomeIcon icon={faGithub}></FontAwesomeIcon>{" "}
                            Github
                          </a>
                        </div>
                        <div className="profile__links">
                          <a
                            className="m-2 anchor-style"
                            href="https://www.linkedin.com/in/suhas-bhairav-43127535/"
                            target="_blank"
                            rel="noreferrer"
                          >
                            <FontAwesomeIcon
                              icon={faLinkedin}
                            ></FontAwesomeIcon>{" "}
                            Linkedin
                          </a>
                          
                        </div>
                        <div className="profile__links__email">
                          <a
                            className="m-2 anchor-style"
                            href="mailto:suhasbhairav@gmail.com"
                            target="_blank"
                            rel="noreferrer"
                          >
                            suhasbhairav@gmail.com
                          </a>
                          
                        </div>
                      </Card.Body>
                    </Card>
                  </div>
                </div>
              </Card>
            </div>
            <div className="background__content col-md-6 d-flex justify-content-around">
              <Card className="shadow-lg p-3 mb-5 bg-body rounded">
                <ul>
                  <li>Software Developer/Architect &amp; Computer Science Researcher</li>
                  <li>Experience: ~8 years</li>
                  <li>
                    Interests: Web Development, Machine Learning and AI
                  </li>
                  <li>
                    Technologies: Java, Spring Boot, Kotlin, Python, JavaScript
                  </li>
                  <li>JavaScript Frameworks: React JS (preferred), Angular</li>
                  <li>
                    Machine Learning Libraries: NLTK, Sci-Kit Learn, Gensim
                  </li>
                </ul>
              </Card>
            </div>
          </div>
        </div>
      </div>
      <div>
          <Works />
      </div>
      <div>
        <Publications />
      </div>
    </div>
  );
};

export default Home;
