import React from "react";
import { Card, Carousel } from "react-bootstrap";
import amazon from "./images/amazon.PNG";
import covid from "./images/covid.PNG";
import twitter from "./images/twitter.PNG";
import "./Works.css";
const Works = () => {
  return (
    <div>
        <div className="works__list">
            <span >
                Works
            </span>
        </div>
      <div className="container">
        <div className="row">
          <div className="col-lg-6 col-md-6">
              <a href="https://twitter-clone-c1117.web.app/" target="_blank">
              <Card>
                <img src={twitter}></img>
                <Card.Title className="works__text">
                    A Twitter Clone using React JS &amp; Google Firebase
                </Card.Title>
            </Card></a></div>

          <div className="col-lg-6 col-md-6"><a href="https://covid-19-tracker-543e2.web.app/" target="_blank"><Card>
                <img src={covid}></img>
                <Card.Title className="works__text">
                    A Covid-19 Tracker using React JS &amp; Google Firebase
                </Card.Title>
            </Card></a></div>
        </div>
        <div className="row projects__2">
            
          <div class="col-lg-6 col-md-6"><a href="https://challenge-4b0f2.web.app/login" target="_blank">
            <Card> 
                <img src={amazon}></img>
                <Card.Title className="works__text">
                    A Fake Amazon Clone using React JS &amp; Google Firebase
                </Card.Title>
            </Card></a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Works;
